@header:#419641;
@headerFont:#FFFFEB;
@bodyColor: #D3E5D3;

* { box-sizing:border-box; }

.b-cont{
    margin-top: -2%;
    margin-left: 0;
	}

@media (min-width: 768px) {
	.b-cont{
	       margin-left: 38%;
	}	
}

@media (min-width: 1330px){
	.b-cont{
		    margin-left: 14%;
	}
}


body {
    background-color: @bodyColor !important;
}

header {
    background-color:@header ;
    color:@headerFont ;
    padding:1% 3%;
    font-family: 'Dancing Script', cursive;
    
    p {
    font-family:sans-serif;
	}
}
